/* global  */
/* vars */
:root{
    --brand_color1: #040F1A;
    --brand_color2: #FF3300;
    --textUnselected_color: #818181;

    --opacity: rgba(1,1,6,0.7);
}
/* basic */
html {
    font-size: 10px;
    scroll-behavior: smooth;
}
body {
    font-family: 'Lato', sans-serif;
}
.container{
    width: 80%;
    margin: 0 auto;
    height: 100%;

    display: inherit;
    justify-content: inherit;
    align-items: inherit;
    flex-direction: inherit;
    flex-wrap: inherit;

    position: relative;
}
* {
    box-sizing: border-box;
    list-style: none;
    padding: 0;
    margin: 0;
}
h1,h2,h3,h4,h5,h6,p {
	margin: 0;
}
/* icon fonts  */
@font-face {
    font-family: 'icomoon';
    src:  url('./fonts/icomoon.eot?k6tzaj');
    src:  url('./fonts/icomoon.eot?k6tzaj#iefix') format('embedded-opentype'),
      url('./fonts/icomoon.ttf?k6tzaj') format('truetype'),
      url('./fonts/icomoon.woff?k6tzaj') format('woff'),
      url('./fonts/icomoon.svg?k6tzaj#icomoon') format('svg');
    font-weight: normal;
    font-style: normal;
    font-display: block;
  }
[class^="icon-"], [class*=" icon-"] {
    /* use !important to prevent issues with browser extensions that change fonts */
    font-family: 'icomoon' !important;
    speak: none;
    font-style: normal;
    font-weight: normal;
    font-feature-settings: normal;
    font-variant: normal;
    text-transform: none;
    line-height: 1;
  
    /* Better Font Rendering =========== */
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
  }
.icon-shopping-cart:before {
    content: "\e904";
  }
.icon-fastfood:before {
    content: "\e900";
  }
.icon-delivery_dining:before {
    content: "\e901";
  }
.icon-cc-visa:before {
    content: "\f1f0";
  }
.icon-cc-mastercard:before {
    content: "\f1f1";
  }
.icon-arrow-long-right:before {
    content: "\e902";
  }
.icon-user-solid-circle:before {
    content: "\e905";
  }
.icon-shop:before {
    content: "\e903";
  }
/* header  */
.header{
    background: url(./img/bg2.jpg) center center / cover no-repeat;
    display: flex;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    justify-content: space-between;
    align-items: center;
    height: 7rem;
    z-index: 9999;
}
.header_title{
    color: white;
    font-size: 2.5rem;
    font-weight: bolder;
    cursor: pointer;
    line-height: 7rem;

    font-family: cursive;
}
.header_nav{
    display: inherit;
    justify-content: inherit;
    align-items: inherit;
    flex-direction: inherit;
    flex-wrap: inherit
}
.header_nav li{
        color: white;
        font-size: 1.6rem;
        font-weight: lighter;
        cursor: pointer;
        line-height: 7rem;
        padding: 0 1rem;
        transition: 450ms all;
    }
.header_nav li:hover{
        color: var(--brand_color1);
        background-color: white;
    }
/* hero */
.hero{
    padding-top: 7rem;
    background: url(./img/bg.jpg) center center / cover no-repeat;
    position: relative
}
.hero .hero_wrapper{
        display: flex;
        flex-direction: column;
        align-items: center;
        height: 100%;

        padding: 10rem 0
    }
.hero .hero_wrapper h1{
            color: white;
            font-size: 10rem;
            margin-bottom: 5rem;
            font-family: cursive;
        }
.hero .hero_wrapper p{
            color: white;
            font-weight: lighter;
            font-size: 2rem;
            margin-bottom: 5rem;
        }
.hero .hero_wrapper .hero_cta{
            margin-bottom: 5rem
        }
.hero .hero_wrapper .hero_cta form input{
                    outline: none;
                    padding: 2rem 3rem;
                    border: none;
                    border-radius: .2rem;
                    font-size: 1.6rem;
                }
.hero .hero_wrapper .hero_cta form .form_search{
                    margin-right: .5rem;
                    width: 40rem;
                }
.hero .hero_wrapper .hero_cta form .form_submit{
                    outline: none;
                    background-color: var(--brand_color2);
                    color: white;
                    transition: 450ms all;
                    cursor: pointer;
                }
.hero .hero_wrapper .hero_cta form .form_submit:hover{
                    background-color: rgb(170, 34, 0);
                }
.hero .hero_wrapper .hero_steps{
            display: flex;
            justify-content: space-between;
            width: 60%
        }
.hero .hero_wrapper .hero_steps .hero_steps_item{
                display: flex;
                flex-direction: column;
                align-items: center
            }
.hero .hero_wrapper .hero_steps .hero_steps_item i{
                    color: white;
                    font-size: 4rem;  
                    margin-bottom: 1rem;              
                }
.hero .hero_wrapper .hero_steps .hero_steps_item p{
                    font-size: 1.6rem
                }
.hero .hero_wrapper .hero_steps .hero_steps_item p span{
                        margin-right: 1rem;
                        font-size: 1.8rem;
                        font-weight: bolder;
                    }
.hero::before{
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: var(--opacity);
}
/* animation  */
/* feature foods */
.feature_food{
    display: flex;
    flex-direction: column;
    align-items: center
}
.feature_food h2, .feature_food p, .feature_food h3, .feature_food span{
        color: var(--brand_color1);
    }
.feature_food h2{
        font-size: 3rem;
    }
.feature_food p{
        font-size: 2rem;
    }
.feature_food .food_restaurants_wrapper{
        display: flex;
        justify-content: space-between;
        width: 100%
    }
.feature_food .food_restaurants_wrapper .food_restaurants_item{
            width: 30%
        }
.feature_food .food_restaurants_wrapper .food_restaurants_item .food_restaurant_img{
                height: 20rem;
                width: 100%;
                position: relative;
            }
.feature_food .food_restaurants_wrapper .food_restaurants_item .food_restaurant_img::after{
                content: "";
                position: absolute;
                top: 0;
                left: 0;
                right: 0;
                bottom: 0;
                background-color: var(--opacity);
                cursor: pointer;
                transition: 450ms all;
            }
.feature_food .food_restaurants_wrapper .food_restaurants_item .food_restaurant_img:hover::after{
                background-color: rgba(0, 0, 0, 0.3);
            }
.feature_food .food_restaurants_wrapper .food_restaurants_item .food_restaurant_content{
                height: 20rem;
                position: relative;
                padding: 1rem;
                transition: 450ms all
            }
.feature_food .food_restaurants_wrapper .food_restaurants_item .food_restaurant_content h3{
                    font-size: 2.2rem;
                    margin-bottom: 1rem;
                }
.feature_food .food_restaurants_wrapper .food_restaurants_item .food_restaurant_content p{
                    font-size: 1.6rem;
                    font-weight: lighter;
                }
.feature_food .food_restaurants_wrapper .food_restaurants_item .food_restaurant_content span{
                    font-size: 2.2rem;
                    font-weight: bolder;
                    position: absolute;
                    bottom: 1rem;
                    left: 1rem;
                }
.feature_food .food_restaurants_wrapper .food_restaurants_item .food_restaurant_content button{
                    outline: none;
                    position: absolute;
                    bottom: 1rem;
                    right: 1rem;
                    padding: 1rem 2rem;
                    border: none;
                    border-radius: .2rem;
                    font-size: 1.6rem;
                    background-color: var(--brand_color2);
                    color: white;
                    transition: 450ms all;
                    cursor: pointer;
                }
.feature_food .food_restaurants_wrapper .food_restaurants_item .food_restaurant_content:hover{
                box-shadow: 2px 5px 20px 0px rgba(0,0,0,0.35);
            }
.feature_food .food_restaurants_wrapper .food_restaurants_item .food_restaurant_img.a{
                background: url(./img/bg.jpg) center center / cover no-repeat;
            }
.feature_food .food_restaurants_wrapper .food_restaurants_item .food_restaurant_img.b{
                background: url(./img/bg.jpg) center center / cover no-repeat;
            }
.feature_food .food_restaurants_wrapper .food_restaurants_item .food_restaurant_img.c{
                background: url(./img/bg.jpg) center center / cover no-repeat;
            }
